import Tablejison from "@/assets/json/Table.json";
import { Users }  from "@/api/api" ; 
import { Switch ,Dialog } from 'vant';
export default {
  name: "formTable",
  components:{Switch,Dialog},
  data() {  
    return {
      TableAcitve: false,
      screenHeight:null,
      Tbody: ["玩家名称", "玩家编号", "当前在线状态", "注册时间",  "ID编号","累积充值金额(千元)","最后登陆IP地址","最后登陆时间"],
      Table: [],
      Tablelist: [],
      page: 1, //当前页数
      pages: null, //总页数
      conter:10, //每页条数
      Total: null,//总条数
      Selection:[] ,//选中
      UserName:"",//玩家姓名
      UserState:"",//玩家状态
      dialogShow:false,
      dialogUsername:"",//操作文员
      dialogPassword:"",//操作密码
      Suershow:false,
      leaguer:null,                  
      GameType:"" //玩家类型
    };
  },
  created() {
      let clientHeight = document.documentElement.clientHeight;
      this.conter = Math.floor((clientHeight - 450) / 53);
      if(this.conter < 5){
        this.conter = 5
      }
      this.screenHeight = clientHeight - 370
      this.User()
  },
  mounted() {},
  methods: {
     async User(){
             let res = await Users({
                page:this.page,
                limit:this.conter,
                name:this.UserName,
             })
              .then(res=>{ 
                 console.log(res.data)
                  this.Tablelist = res.data
                  this.Total = res.meta.total;
                  this.pages = res.meta.last_page        
              })
    },
    conetrModer(){
          if(this.page > this.pages ){
            this.page = this.pages
          }
          if(this.page < 1 ){
            this.page = 1
         }
    },
    TotalPaging() {
       
        this.User()
    },
    Prev(){ //上一页    
          console.log("上一页")
          this.page --
          this.User();
    },
    next(){//下一页
          console.log("下一页")
          this.page ++ 
          this.User();
    },
    AllSwitch(){//全选全不选
        this.Tablelist = [...this.Tablelist]
        this.TableAcitve =! this.TableAcitve
        this.Selection = []
        
        if(this.TableAcitve){
            this.Tablelist.map(item=>{
               item.tion = true 
            })
            for(var i = 0;i< this.TableAcitve.length;i++){
                this.TableAcitve[i].dex = i
                this.Selection.push(item)
            }
            return
        }else{
           this.Tablelist.map(item=>{
               item.tion = false   
            }) 
        }
    },
    ToSwitch(index){//单选
           this.Tablelist = [...this.Tablelist]
           this.Tablelist[index].tion =! this.Tablelist[index].tion
            this.Selection = [] 
           this.Tablelist.map(item=>{
                if(item.tion){
                  item.dex = index
                  this.Selection.push(item)
                }
           })
           console.log( this.Selection)
    },
    PlayerDetails(item){
        this.$storage.setSession("UserDetails", item);
        this.$router.push({ path: "/UserDetails"}); 
    },
    enter(index){//鼠标移入
       this.Tablelist = [...this.Tablelist]
       this.Tablelist[index].mous = true
    },
    leave(){//鼠标移除
        this.Tablelist = [...this.Tablelist]
        this.Tablelist.map(item=>{
          item.mous = false
          
        })
    },
   
    //导出
    outExe(){
     
          Dialog.confirm({
                  title: '用户表单导出',
                  message: '确认将用户列表导出为excel吗？'
                }).then(() => {
                     this.Export2Excel()
                     alert("导出成功")
                }).catch(() => {
                     alert("导出失败")
                });
    },
    async Export2Excel() {
          var that = this;
          let res = await Users({
                page:1,
                limit: 99999,
             })
              .then(res=>{ 
                   
                require.ensure([], () => {
                   
                    const { export_json_to_excel } = require('../../excel/Export2Excel'); //这里必须使用绝对路径
                    const list = res.data;
                    const tHeader = ["玩家名称", "玩家编号", "当前在线状态", "注册时间",  "ID编号","累积充值金额(元)","最后登陆IP地址","最后登陆时间"]; // 导出的表头名
                    const filterVal =  ['name','userid', 'mous', 'registertime','roomid','name','energe','energelimit']; // 导出的表头字段名
                 
                    const data = that.formatJson(filterVal, list);
                    export_json_to_excel(tHeader, data, `用户列表`);// 导出的表格名称，根据需要自己命名
                })    
             })
         
    },
    formatJson(filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => v[j]))
    },
    //查询
    QuerySuer(){
        let UserName = this.UserName
        let UserState = this.UserState

    },
    SuerDelete(){
        console.log("删除玩家")
    },
    AddSuer(){
        //this.Suershow = true
    },
     beforeClose(action, done) {
      if (action === 'confirm') {
         let dialogUsername = this.dialogUsername
         let dialogPassword = this.dialogPassword
      
         if(dialogUsername == dialogPassword){
           this.Suershow = true
         }else{
           alert("您没有添加权限")
         }
          setTimeout(done, 100);
        
      } else {
        done();
      }
    },
   
     SuerSer(){
      this.Suershow = false
     },
      cancelSer(){
           this.Suershow = false
      },
  }
};
