import Tablejison from "@/assets/json/Table.json"; 
export default {
  name: "NinBlacklist",
  components:{},
  data() {  
    return {
      TableAcitve: false,
      screenHeight:null,
      Tbody: ["玩家名称","所在服务器", "玩家ID", "禁言时间","禁言时长","剩余禁言时长", "禁言原因","操作人员",  "提前解除禁言"],
      Table: [],
      Tablelist: [],
      page: 1, //当前页数
      pages: null, //总页数
      conter: 10, //每页条数
      Total: null,//总条数
      Selection:[] ,//选中
      ForbiddenWords:false,
      PlayName:"",
      PlayID:"",
      PlayServe:"",
      PlayerList:[],
    };
  },
  created() {
      this.Table = Tablejison;
      let clientHeight = document.documentElement.clientHeight;
      this.conter = Math.floor((clientHeight - 450) / 50);
      if(this.conter < 5){
        this.conter = 5
      }
      this.screenHeight = clientHeight - 370
      this.TotalPaging();
  },
  mounted() {},
  methods: {
    TotalPaging() {
      this.Selection = []
      this.Tablelist =[]
      this.Total = this.Table.length;
      this.pages = Math.ceil(this.Total / this.conter);
      for ( var i = (this.page - 1) * this.conter; i < this.page * this.conter;i++) {
        if(this.Table[i] != undefined){ 
          this.Table[i].tion = false;
          if(this.Table[i].state == "未完成" || this.Table[i].state == "等待中" ){
            this.Table[i].Finish = "否"
          }else{
             this.Table[i].Finish = "是"
          }
            this.Tablelist.push(this.Table[i]);
        } 
      }
    },
    Prev(){ //上一页    
          console.log("上一页")
          this.page --
          this.TotalPaging();
    },
    next(){//下一页
          console.log("下一页")
          this.page ++ 
          this.TotalPaging();
    },
    enter(index){//鼠标移入
        this.Tablelist = [...this.Tablelist]
        this.Tablelist[index].mous = true
      },
      leave(){//鼠标移除
          this.Tablelist = [...this.Tablelist]
          this.Tablelist.map(item=>{
            item.mous = false
          })
      },
  

    
   
    NinBlack(index){
        this.ForbiddenWords = true;
    },
     AddPlayerBlack(){
      this.ForbiddenWords = false;
    },
    NoForbiddenWords(){
        this.ForbiddenWords = false;
    },
    PlayerName(){//玩家选择
          let a = []
          let PlayName = this.PlayName
          if(PlayName ==''){
            this.PlayerList = []
            return
          }
         
          this.Table.map(item=>{
             if(item.name.indexOf(`${PlayName}`) != -1){
               a.push(item)
             }
          })
         this.PlayerList = a
    },
    PlayerID(){
          let a = []
          let PlayID = this.PlayID
          if(PlayID ==''){
              this.PlayerList = []
            return
          }
          this.Table.map(item=>{
             if(item.ID.indexOf(`${PlayID}`) != -1){
               a.push(item)
             }
          })
         this.PlayerList = a
    },
    PlayerServe(){
          let a = []
          let PlayServe = this.PlayServe
          if(PlayServe ==''){
            this.PlayerList = []
             return
          }
          this.Table.map(item=>{
             if(item.phone.indexOf(`${PlayServe}`) != -1){
               a.push(item)
             }
          })
         this.PlayerList = a
    },
    ChoicePlay(item){
      this.PlayName = item.name
      this.PlayID = item.ID
      this.PlayServe = item.phone
       this.PlayerList = []
    },
   
  }
};
