import { Switch ,Dialog } from 'vant';

import Tablejison from "@/assets/json/Table.json";
import XTime  from "@/components/Section/Time.vue";
export default {
  name: "Server",
  components:{Switch,Dialog,XTime},
  data() {  
    return {
      TableAcitve: false,
      screenHeight:null,
      Tbody: ["服务器编号", "服务器名称", "服务器状态", "开服时间",  "累计注册人数","累计收入金额(千元)","累计付费人数","当前在线人数","游戏入口开关操作"],
      Table: [],
      Tablelist: [],
      page: 1, //当前页数
      pages: null, //总页数
      conter: 10, //每页条数
      Total: null,//总条数
      Selection:[] ,//选中
      ServeName:'', //服务器名称
      MonthState:"",//服务器状态
      dialogShow:false,//Dialog状态
      Serveshow:false,
      username: '',
      password: '',
      TimeDaty:false,
      Timechoice:"",
    };
  },

  created() {
      this.Table = Tablejison;

      let clientHeight = document.documentElement.clientHeight;
      this.conter = Math.floor((clientHeight - 450) / 50);
      if(this.conter < 5){
        this.conter = 5
      }
      this.screenHeight = clientHeight - 370

      this.TotalPaging();

  },
  mounted() { 
    let a = document.getElementById('elem').offsetHeight
    let clientHeight = document.documentElement.clientHeight;
    this.conter = Math.floor((clientHeight - 450) / a);
    if(this.conter < 5){
        this.conter = 5
      }
    this.TotalPaging();
  },
  methods: {
    TotalPaging() {
      this.Selection = []
      this.Tablelist =[]
      this.Total = this.Table.length;
      this.pages = Math.ceil(this.Total / this.conter);
      for ( var i = (this.page - 1) * this.conter; i < this.page * this.conter;i++) {
        if(this.Table[i] != undefined){ 
          this.Table[i].mous = false;
            this.Tablelist.push(this.Table[i]);
        } 
      }
      console.log( this.Tablelist)
      this.Tablelist.map(item=>{ 
           if(item.state == "在线" || item.state == "开服"){
               item.state = "开服"
               item.tion = true;
          }else{
              item.state = "未开服"
              item.tion = false;
          }
      })
    },
     enter(index){//鼠标移入
       this.Tablelist = [...this.Tablelist]
       this.Tablelist[index].mous = true
    },
    leave(){//鼠标移除
        this.Tablelist = [...this.Tablelist]
        this.Tablelist.map(item=>{
          item.mous = false
        })
    },
    Prev(){ //上一页    
          console.log("上一页")
          this.page --
          this.TotalPaging();
    },
    next(){//下一页
          console.log("下一页")
          this.page ++ 
          this.TotalPaging();
    },
    PlayerDetails(){ //服务器详情
        this.$router.push({ path: "/ServerDetails"}); 
    },
    QuerySer(){ //查询
         
        //  ServeName:'', //服务器名称  MonthState:"",//服务器状态 
       console.log(this.ServeName,"1111111111",this.MonthState)
    },
    SerSwitch(open){

        console.log(open,"服务器开关")
    },
    //导出
    outExe(){
     
          Dialog.confirm({
                  title: 'excel导出',
                  message: '确认将服务器列表导出为excel吗？'
                }).then(() => {
                     this.Export2Excel()
                }).catch(() => {
                  // on cancel
                  });
    },
    Export2Excel() {
          var that = this;
          require.ensure([], () => {
              const { export_json_to_excel } = require('../../excel/Export2Excel'); //这里必须使用绝对路径
              const tHeader = ['name','phone', 'state', 'Finish','Number','ID','tion','pid']; // 导出的表头名
              const filterVal = ['name','phone', 'state', 'Finish','Number','ID','tion','pid']; // 导出的表头字段名
              const list = Tablejison;
              const data = that.formatJson(filterVal, list);
              export_json_to_excel(tHeader, data, `服务器列表`);// 导出的表格名称，根据需要自己命名
          })
    },
    formatJson(filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => v[j]))
    },
    //添加
    AddServe(){
       this.dialogShow = true
    },
     beforeClose(action, done) {
      if (action === 'confirm') {
         let username = this.username
         let password = this.password
         console.log(username,password)
         if(username == password){
           this.Serveshow = true
         }else{
           alert("您没有添加权限")
         }
          setTimeout(done, 100);
        
      } else {
        done();
      }
    },
    OpnTimeDaty(){
      this.TimeDaty = true
    },
    cancel(){
       this.TimeDaty = false
    },
    Sure(time){
        this.TimeDaty = false
        this.Timechoice = time
    },
    cancelSer(){
      this.Serveshow = false
    },
    SuerSer(){
      this.Serveshow = false
    }
  }
};
