import MainLeftjison from "@/assets/json/MainLeft.json";
import { ersonalDetails } from "@/api/api"
export default {
    name:"mainleft",
    components: {},
    data() {
        return {
            isActive:0,
            active:0,
            imags:require("@/assets/img/toux.png"),
            list: [],
            erDetails:{},
        };
    },
    mounted() {},
    created(){   
      var _this = this;
      _this.list = MainLeftjison
      let state =  this.$storage.getSession('state')
      if(state != '' && state != undefined && state != null){
          _this.active =  state.RightActive
          _this.isActive =  state.Listactive   
             _this.list.map(item =>{
                 item.active = false
             })
          _this.list[state.RightActive].active = true
      }  

       this.Details()
  
  }, 
    methods: {
         async Details(){
             let res = await ersonalDetails({})
              .then(res=>{
                  console.log(res,"@@@@@@@@")
                  this.erDetails = res
              })
       
        },
        animation(index,item){
            if(this.active !=index){
                this.isActive = 0
            }
            this.active = index
            this.list = [...this.list]
             if(this.list[index].active){
               this.list[index].active = false
             }else{
                this.list.map(item=>{
                    item.active = false
                })
                this.list[index].active = true
             }
            
              this.$emit('animation',item,index)
            //this.list[index].active =!  this.list[index].active
        },
        listanim(type,index){
            console.log(type)
            this.isActive = index
           this.$emit('listanim',type.Listactive,index)
        },
     
    }
}
