import ScatterStreamJson from "@/assets/json/ScatterStream.json";
export default {
  name: "ScatterStream",
  components: {},
  data() {
    return {};
  },
  created() {},
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      let myChart = this.$echarts.init(document.getElementById("myChart"));

      myChart.setOption({
        title: {
          text: "Dispersion of house price based on the area",
          x: "center",
          y: 0,
          textStyle: {
            color: "#3259B8",
            fontSize: 16,
            fontWeight: "normal"
          }
        },
        visualMap: {
          min: 15202,
          max: 159980,
          dimension: 1,
          left: "right",
          top: "top",
          text: ["HIGH", "LOW"], // 文本，默认为数值文本
          calculable: true,
          itemWidth: 18,
          itemHeight: 160,
          textStyle: {
            color: "#3259B8",
            height: 56,
            fontSize: 11,
            lineHeight: 60
          },
          inRange: {
            color: ["#3EACE5", "#F02FC2"]
          },
          padding: [50, 20],
          orient: "horizontal"
        },
        grid: {
          left: "5%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        tooltip: {
          trigger: "item",
          showDelay: 0,
          formatter: function(params) {
            if (params.value.length > 1) {
              return (
                "Area: " +
                params.value[0] +
                "㎡<br/> " +
                "House price: " +
                params.value[1] +
                " CNY/㎡ "
              );
            } else {
              return (
                params.seriesName +
                " :<br/>" +
                params.name +
                " : " +
                params.value +
                " CNY/㎡ "
              );
            }
          },
          axisPointer: {
            show: true,
            type: "cross",
            lineStyle: {
              type: "dashed",
              width: 1
            }
          }
        },

        xAxis: [
          {
            type: "value",
            scale: true,
            axisLabel: {
              formatter: "{value} m"
            },
            nameTextStyle: {
              color: "#3259B8",
              fontSize: 14
            },
            axisTick: {
              show: false
            },
            axisLine: {
              lineStyle: {
                color: "#3259B8"
              }
            },
            splitLine: {
              show: false
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            scale: true,
            axisLabel: {
              formatter: "{value} CNY/㎡"
            },
            nameTextStyle: {
              color: "#3259B8",
              fontSize: 14
            },
            axisTick: {
              show: false
            },
            axisLine: {
              lineStyle: {
                color: "#3259B8"
              }
            },
            splitLine: {
              show: false
            }
          }
        ],
        series: [
          {
            name: "price-area",
            type: "scatter",
            data: ScatterStreamJson,
            symbolSize: 4
          }
        ]
      });
    }
  }
};
