import Tablejison from "@/assets/json/Table.json";
import {
  adminIstrators,
  getRoles,
  getPermissions,
  postAdminIstrators,
  DistAdminIstrators,
  putAdminIstrators
} from "@/api/api";
import { Dialog } from "vant";
export default {
  name: "Admin",
  components: { Dialog },
  data() {
    return {
      TableAcitve: false,
      boxSwichAcitve: true,
      screenHeight: null,
      Tbody: [
        "管理员名称",
        "管理员编号",
        "注册时间",
        "管理员角色",
        "管理员权限",
        "操作"
      ],
      Table: [],
      Tablelist: [],
      page: 1, //当前页数
      pages: null, //总页数
      conter: 10, //每页条数
      Total: null, //总条数
      Selection: [], //选中
      UserName: "", //玩家姓名
      UserState: "", //玩家状态
      dialogShow: false,
      dialogUsername: "", //登录账号
      dialogPassword: "", //登录密码
      dialogUsername1: "", //管理员名称
      dialogPassword1: "", //确认密码
      Suershow: false,
      leaguer: null,
      boxUlShow: false,
      boxSwichTitle: 1,
      OperationType: 0,
      RolesWork: "",
      RolesWork1: [], //角色
      GameType: [], //玩家类型
      RolesList: [],
      permissionsList: [],
      EditId: null
    };
  },
  created() {
    // this.Table = Tablejison;
    let clientHeight = document.documentElement.clientHeight;
    this.conter = Math.floor((clientHeight - 450) / 50);
    if(this.conter < 5){
        this.conter = 5
    }
    this.screenHeight = clientHeight - 370;
    this.Admin();
  },
  mounted() {},
  methods: {
    async Roles() {
      let res = await getRoles({}).then(res => {
        res.data.map(item => {
          item.Active = false;
        });
        this.RolesList = res.data;
      });
    },
    async Permissions() {
      let res = await getPermissions({}).then(res => {
        res.data.map(item => {
          item.Active = false;
        });
        this.permissionsList = res.data;
      });
    },
    async Admin() {
      let res = await adminIstrators({
        page: 1,
        limit: this.conter
      })
        .then(res => {
          for (var i = 0; i < res.data.length; i++) {
            let a, b;
            for (var j = 0; j < res.data[i].roles.length; j++) {
              a = res.data[i].roles[0].title;
            }
            for (var j = 0; j < res.data[i].permissions.length; j++) {
              b = res.data[i].permissions[0].title;
            }
            res.data[i].permissionsTitle = b;
            res.data[i].rolesTitle = a;
          }
          this.Tablelist = res.data;
          this.Total = res.meta.total;
          this.pages = res.meta.last_page;

          if (this.RolesList == "") {
            this.Roles();
            this.Permissions();
          }
        })
        .catch(err => {});
    },
    Eliminate() {
      this.RolesList.map(item => {
        item.Active = false;
      });
      this.permissionsList.map(item => {
        item.Active = false;
      });
      this.dialogUsername = this.dialogUsername1 = this.dialogPassword = this.dialogPassword1 =
        "";
    },

    AddSuer() {
      //添加管理员
      this.Eliminate();
      //this.dialogShow = true
      this.Suershow = true;
      this.OperationType = 0;
    },
    async Toexamine(item) {
      // 编辑管理员
      this.Eliminate();
      this.OperationType = 1;
      this.EditId = item.id;
      let res = await DistAdminIstrators({
        ID: item.id
      }).then(res => {
        this.Suershow = true;
        this.boxUlShow = true;
        //赋值
        this.dialogUsername1 = res.data.name;
        this.dialogUsername = res.data.username;

        res.data.roles.map(res => {
          this.RolesList.map(item => {
            if (res.title == item.title) {
              item.Active = true;
            }
          });
        });
        res.data.permissions.map(res => {
          this.permissionsList.map(item => {
            if (res.title == item.title) {
              item.Active = true;
            }
          });
        });
      });
    },
    addRoles(num) {
      this.boxUlShow = true;
      this.boxSwichTitle = num;
    },
    TotalPaging() {
      this.Admin();
    },
    Prev() {
      //上一页

      this.page--;
      this.Admin();
    },
    next() {
      //下一页

      this.page++;
      this.Admin();
    },
    boxSwich(item, index, num) {
      if (num == 1) {
        //角色
        this.RolesList = [...this.RolesList];
        this.RolesList[index].Active = !this.RolesList[index].Active;
      }
      if (num == 0) {
        //权限
        this.permissionsList = [...this.permissionsList];
        this.permissionsList[index].Active = !this.permissionsList[index]
          .Active;
      }
    },

    enter(index) {
      //鼠标移入
      this.Tablelist = [...this.Tablelist];
      this.Tablelist[index].mous = true;
    },
    leave() {
      //鼠标移除
      this.Tablelist = [...this.Tablelist];
      this.Tablelist.map(item => {
        item.mous = false;
      });
    },

    //导出
    outExe() {
      Dialog.confirm({
        title: "用户表单导出",
        message: "确认将用户列表导出为excel吗？"
      })
        .then(() => {
          this.Export2Excel();
        })
        .catch(() => {
          // on cancel
        });
    },
    Export2Excel() {
      var that = this;
      require.ensure([], () => {
        const { export_json_to_excel } = require("../../excel/Export2Excel"); //这里必须使用绝对路径
        const tHeader = [
          "name",
          "phone",
          "state",
          "Finish",
          "Number",
          "ID",
          "tion",
          "pid"
        ]; // 导出的表头名
        const filterVal = [
          "name",
          "phone",
          "state",
          "Finish",
          "Number",
          "ID",
          "tion",
          "pid"
        ]; // 导出的表头字段名
        const list = Tablejison;
        const data = that.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, `用户列表`); // 导出的表格名称，根据需要自己命名
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
    //添加角色 权限

    NoperationBoxUlShow() {
      this.boxUlShow = false;
    },
    //查询
    QuerySuer() {
      let UserName = this.UserName;
      let UserState = this.UserState;
    },

    beforeClose(action, done) {
      if (action === "confirm") {
        let dialogUsername = this.dialogUsername;
        let dialogPassword = this.dialogPassword;

        if (dialogUsername == dialogPassword) {
          this.Suershow = true;
        } else {
          alert("您没有添加权限");
        }
        setTimeout(done, 100);
      } else {
        done();
      }
    },

    async SuerSer() {
      // this.Suershow = false
      let a = [];
      let b = [];
      this.RolesList.map(item => {
        if (item.Active) {
          a.push(item.name);
        }
      });
      this.permissionsList.map(item => {
        if (item.Active) {
          b.push(item.name);
        }
      });

      if (a.length == 0 || b.length == 0) {
        alert("请添加角色类型和权限类型");
        return false;
      }
      if (this.dialogUsername.length < 6) {
        alert("登录账号最少为6字符");
        return false;
      }
      if (this.dialogUsername.length < 2) {
        alert("管理员名称至少为2个字符");
        return false;
      }
      if (this.OperationType == 0) {
        if (this.dialogPassword.length < 6 || this.dialogPassword1.length < 6) {
          alert("密码至少为2个字符");
          return false;
        }
        if (this.dialogPassword != this.dialogPassword1) {
          alert("两次密码不一致，请确认密码");
        }
      }

      if (this.OperationType == 0) {
        //添加管理员
        let res = await postAdminIstrators({
          username: this.dialogUsername,
          name: this.dialogUsername1,
          password: this.dialogPassword,
          password_confirmation: this.dialogPassword1,
          roles: a,
          permissions: b
        }).then(res => {
          alert("添加成功");
          this.Admin();
          this.boxUlShow = false;
          this.Suershow = false;
        });
        return;
      }
      if (this.OperationType == 1) {
        // 编辑管理员
        if (this.dialogPassword != "" || this.dialogPassword1 != "") {
          if (
            this.dialogPassword.length < 6 ||
            this.dialogPassword1.length < 6
          ) {
            alert("密码至少为2个字符");
            return false;
          }
          if (this.dialogPassword != this.dialogPassword1) {
            alert("两次密码不一致，请确认密码");
          }
          let res = await putAdminIstrators({
            ID: this.EditId,
            username: this.dialogUsername,
            name: this.dialogUsername1,
            password: this.dialogPassword,
            password_confirmation: this.dialogPassword1,
            roles: a,
            permissions: b
          }).then(res => {
            alert("修改成功");
            this.Admin();
            this.boxUlShow = false;
            this.Suershow = false;
          });
          return;
        }

        let res = await putAdminIstrators({
          ID: this.EditId,
          username: this.dialogUsername,
          name: this.dialogUsername1,
          roles: a,
          permissions: b
        }).then(res => {
          alert("修改成功");
          this.Admin();
          this.boxUlShow = false;
          this.Suershow = false;
        });
      }
    },
    cancelSer() {
      this.Suershow = false;
    }
  }
};
