import { getRoles, getPermissions , postRoles , postPermissions , putRoles , putPermissions ,DeleteRoles , DeletePermissions} from "@/api/api";

export default {
  name: "AdminJurisdiction",
  components: {},
  data() {
    return {
      screenHeight: null,
      RolesList: null,
      permissionsList: null,
      fidexBox: false,
      boxUlShow: false,
      addType: 0, //添加类型 0=》角色 1 ==》权限 //编辑类型 2=》角色 3 ==》权限
      EditID: 0, 
      keyIndex: null, //操作下标
      AdName: "角色添加",
      Title:"",
      Name:"",
    };
  },
  created() {
    let clientHeight = document.documentElement.clientHeight;
    //this.conter = Math.floor((clientHeight - 450) / 53);
    this.screenHeight = clientHeight - 370;
    this.Roles();
    this.Permissions();
  },
  mounted() {},
  methods: {
    async Roles() { //角色列表
      let res = await getRoles({}).then(res => {
        res.data.map(item => {
          item.Active = false;
        });

        this.RolesList = res.data;
        console.log(this.RolesList);
      });
    },
    async Permissions() { //权限列表
      let res = await getPermissions({}).then(res => {
        res.data.map(item => {
          item.Active = false;
        });
        this.permissionsList = res.data;
        console.log(this.permissionsList);
      });
    },
    async addPostRoles(){ //添加角色
        let a = []
        this.permissionsList.map(item => {
            if (item.Active) {
            a.push(item.name);
            }
        });
        if(a == ''){alert("请未角色绑定内置权限！！！")}
        let res =postRoles({
               name:this.Name,
               title:this.Title ,
               permissions:a,
        }).then(res=>{
            alert("添加成功")
            this.box()
            this.Roles()
        }).catch(reen=>{
            alert("添加失败")
        }) 
    },
    async addPermissions(){ //添加权限
            let res = await postPermissions({
                name:this.Name,
                title:this.Title ,
            }).then(res=>{
                alert("添加成功")
                this.Permissions()
                this.box()
            }).catch(reen=>{
                alert("添加失败")
            }) 
    } ,
    async EidtRoles(){ //编辑角色
        let a = []
        this.permissionsList.map(item => {
            if (item.Active) {
               a.push(item.name);
            }
        });
        if(a == ''){alert("请未角色绑定内置权限！！！")}
        let res =putRoles({
               ID:this.EditID,
               name:this.Name,
               title:this.Title ,
               permissions:a,
        }).then(res=>{
            console.log(res.data)
             this.Roles()
            this.box()
             alert("修改成功")
        }).catch(reen=>{
            alert("修改失败")
        }) 
    }, 
    async EidtPermissions(){ //编辑权限
            let res = await putPermissions({
                ID:this.EditID,
                name:this.Name,
                title:this.Title ,
            }).then(res=>{
               this.Permissions()
               this.box()
               alert("修改成功")
            }).catch(reen=>{
                alert("修改失败")
            }) 
    } ,
    async DeleRoles(item){ //删除角色
       let res = await DeleteRoles({
            ID:item.id
       }).then(res=>{
                this.Roles()
                this.box()
                alert("删除成功")
            }).catch(reen=>{
                alert("删除失败") 
            })
    },
    async DelePermissions(item){ //删除权限
           console.log(item)
            let res = await DeletePermissions({
                    ID:item.id
            }).then(res=>{
                        this.Permissions()
                        this.box()
                        alert("删除成功")
                    }).catch(reen=>{
                        alert("删除失败")
                    })
    },
    SubFrom(){
        if(this.Title == '' || this.Name == ''){
            alert('请检查所添加的字段(不能为空且长度大于2)')
            return
        }
       if(this.addType == 0){ //添加角色
          this.addPostRoles()
          return
       }
       if(this.addType == 1){ //添加权限
       this.addPermissions()
         return
       }
       if(this.addType == 2){ //编辑角色
          this.EidtRoles()
          return
       }
       if(this.addType == 3){ //编辑权限
         this.EidtPermissions()
          return
       }
    },
    box(){
        this.permissionsList = [...this.permissionsList];
        this.Title ="",
        this.Name = "",
        this.permissionsList.map(item => {
          item.Active = false;
        });
        this.fidexBox = false
        this.boxUlShow = false
    },
    addList(num) {
     
      this.addType = num;
      this.fidexBox = true;
      if (num == 0) {
        this.AdName = "角色添加";
      } else {
        this.AdName = "权限添加";
      }
    },
    Edit(num,item,index) {
        this.EditID = item.id;
        this.fidexBox = true;
        this.addType = num;
        this.keyIndex = index;
      if (num == 2) {
        this.AdName = "角色编辑";
        this.Name = item.name
        this.Title = item.title
         this.permissionsList = [...this.permissionsList];
        item.permissions.map (res=>{
             this.permissionsList.map(item =>{
                if(item.name ==  res.name){
                    item.Active = true
                }
             })
        })
      }else {
        this.AdName = "权限编辑";
        this.Name = item.name
        this.Title = item.title
      }
      
    },
    boxSwich(item, index, num) {
        //权限
        this.permissionsList = [...this.permissionsList];
        this.permissionsList[index].Active = !this.permissionsList[index].Active;
      
    },
    cancel(){
        this.fidexBox = false
        this.boxUlShow = false 
    },
    perationBoxUlShow(){
       this.boxUlShow = true
    },
    NoperationBoxUlShow() {
        this.boxUlShow = false
    }
  }
};
