import Tablejison from "@/assets/json/Table.json"; 
import { Switch ,Dialog } from 'vant';
export default {
  name: "MemberList",
  components:{Switch,Dialog},
  data() {  
    return {
      TableAcitve: false,
      screenHeight:null,
      Tbody: ["名称", "电话号码", "状态", "是否标记",  "ID编号","账户金额(千元)","注册时间"],
      Table: [],
      Tablelist: [],
      page: 1, //当前页数
      pages: null, //总页数
      conter: 10, //每页条数
      Total: null,//总条数
      Selection:[] ,//选中
    };
  },
  created() {
      this.Table = Tablejison;
      let clientHeight = document.documentElement.clientHeight;
      this.conter = Math.floor((clientHeight - 450) / 50);
      if(this.conter < 5){
        this.conter = 5
      }
      this.screenHeight = clientHeight - 370
      this.TotalPaging();
  },
  mounted() {},
  methods: {
    TotalPaging() {
      this.Selection = []
      this.Tablelist =[]
      this.Total = this.Table.length;
      this.pages = Math.ceil(this.Total / this.conter);
      for ( var i = (this.page - 1) * this.conter; i < this.page * this.conter;i++) {
        if(this.Table[i] != undefined){ 
          this.Table[i].tion = false;
          if(this.Table[i].state == "未完成" || this.Table[i].state == "等待中" ){
            this.Table[i].Finish = "否"
          }else{
             this.Table[i].Finish = "是"
          }
            this.Tablelist.push(this.Table[i]);
        } 
      }
    },
    Prev(){ //上一页    
          console.log("上一页")
          this.page --
          this.TotalPaging();
    },
    next(){//下一页
          console.log("下一页")
          this.page ++ 
          this.TotalPaging();
    },
    AllSwitch(){//全选全不选
        this.Tablelist = [...this.Tablelist]
        this.TableAcitve =! this.TableAcitve
        this.Selection = []
        if(this.TableAcitve){
            this.Tablelist.map(item=>{
               item.tion = true 
            })
            for(var i = 0;i< this.TableAcitve.length;i++){
                this.TableAcitve[i].dex = i
                this.Selection.push(item)
            }
            return
        }else{
           this.Tablelist.map(item=>{
               item.tion = false   
            }) 
        }
    },
    ToSwitch(index){//单选
           this.Tablelist = [...this.Tablelist]
           this.Tablelist[index].tion =! this.Tablelist[index].tion
            this.Selection = [] 
           this.Tablelist.map(item=>{
                if(item.tion){
                  item.dex = index
                  this.Selection.push(item)
                }
           })
           console.log( this.Selection)
    },
    enter(index){//鼠标移入
       this.Tablelist = [...this.Tablelist]
       this.Tablelist[index].mous = true
    },
    leave(){//鼠标移除
        this.Tablelist = [...this.Tablelist]
        this.Tablelist.map(item=>{
          item.mous = false
        })
    },
    KeySwitch(){

    }
  }
};
