import Tablejison from "@/assets/json/Table.json"; 
import { getMails , putmails , Deletemails , getMailsID , Users} from "@/api/api";
import Card from "@/assets/json/card.json";
import Prop from "@/assets/json/Prop.json";
export default {
  name: "ActivNoticeToexamine",
  components:{},
  data() {  
    return {
      TableAcitve: false,
      screenHeight:null,
      EmailOperation:false,
      Tbody: ["操作人员","邮件标题", "邮件ID", "邮件内容","邮件状态", "发送时间", "操作"],
      Table: [],
      Tablelist: [],
      page: 1, //当前页数
      pages: null, //总页数
      conter: 10, //每页条数
      Total: null,//总条数
      Selection:[] ,//选中
      EmailOperation:false,
      title:"",
      EmadilData:null,
      configbox:[],
      PropList:[],
       UserList:[],
      UserListBox:[],
      MailsId:null
    };
  },
  created() {
      this.Table = Tablejison;
      let clientHeight = document.documentElement.clientHeight;
      this.conter = Math.floor((clientHeight - 450) / 50);
      if(this.conter < 5){
        this.conter = 5
      }
      this.screenHeight = clientHeight - 370
      this.configure()
      this.Mails()
      this.User()
  },
  mounted() {},
  methods: {
     configure(){
       let a = Card
       this.PropList = a.concat(Prop)
       this.PropList.map(item=>{
                item.mous = false;
                item.tion = false;
                item.size = 1 
        })
     },
      async User(){
       let res = await Users({
            page:1,
            limit:999999999999,
       }).then(res=>{
          this.UserList = res.data
         
       })
     },
     async Mails (){
         let res = await getMails({
              page:this.page,
              limit:this.conter,
              title:this.title,
              check_status:0,
         }).then(res=>{
              console.log(res)
               res.data.map(item=>{
               item.tion = false
               item.check_status_name = "未审核";
               if(item.check_status == 1){
                 item.check_status_name = "已审核";
               }
                if(item.send_status == 1){
                 item.check_status_name = "已发送";
               }
               this.Tablelist = res.data
               this.Total = res.meta.total;
               this.pages = res.meta.last_page  
            })
         })
     },
     async deleteEm(item){
       let res = await Deletemails({
            ID:item.id ,
       }).then(res=>{
             this.EmailOperation = false  
             this.Mails()
             alert("删除成功")
            
        })
      },
    async OKperation(item){
        let res = await putmails({
          ID:item.id ,
          check_status:1,
          send_status:0,
        }).then(res=>{
             this.EmailOperation = false  
             this.Mails()
            alert("审核通过")
             
        })
   },
   Noperation(){
       this.EmailOperation = false    
   },
      EmailConter(item){
        this.MailsId = item.id
        this.MailsData()


        this.configbox = [...this.configbox]
          this.configbox = []
         let a = this.PropList;
         if(item.accessories != null){
            a.map(key=>{   
               //let res = conlosdata
              item.accessories.map(res=>{
                if(key.ID == res.id){
                  res.name = key.name
                
                      this.configbox.push(res)
                  }
              })
           })
         }
        this.EmailOperation = true    
        this.EmadilData = item  
    },
    async MailsData(){
        let res = await getMailsID({
            ID:this.MailsId
        }).then(res=>{
          console.log(res)
          console.log(this.UserList,"|||||||||||||||||||||||||||||||||")
           this.UserListBox = []
           this.UserList.map(item=>{
               res.data.users.map(key=>{
                 if(key == item.userid){
                   this.UserListBox.push(item)
                 }
               })
           })
        })
     },
    TotalPaging() {
       this.Mails()
    },
    Prev(){ //上一页    
        console.log("上一页")
        this.page --
        this.TotalPaging(); 
    },
    next(){//下一页 
        console.log("下一页")
        this.page ++ 
        this.TotalPaging();
    },
    AddActivNotice(){
       let Obj={
            type:"Notice",
            obj:{},  
        } 
            this.$router.push({ path: "/ActivNoticeDetails",query:{Obj:Obj}}); 
    },
     enter(index){//鼠标移入
       this.Tablelist = [...this.Tablelist]
       this.Tablelist[index].mous = true
    },
    leave(){//鼠标移除
        this.Tablelist = [...this.Tablelist]
        this.Tablelist.map(item=>{
          item.mous = false
        })
    },
    //审核弹出框
    Noperation(){
      this.EmailOperation = false
      console.log(39000/2)
    },
    Toexamine(item){
      this.EmailOperation = true
    }
  }
};
