import Xhead  from "./Section/head.vue";
import MainLeftjison from "@/assets/json/MainLeft.json"; //左边导航数组列表数据
import XmainLeft  from "./Section/mainLeft.vue"; //左边导航列表
import XServer  from "./Member/Server.vue"; //服务器总览
import XServerAuditing  from "./Member/ServerAuditing.vue"; //服务器操作审核
import XformTable  from "./Section/formTable.vue"; //用户管理Table
import XMemberList  from "./Member/MemberList.vue"; //会员管理列表
import XActivNotice  from "./Member/ActivNotice.vue"; //公告发送
import XActivEmail  from "./Member/ActivEmail.vue"; //邮件发送
import XActivNoticeToexamine  from "./Member/ActivNoticeToexamine.vue"; //公告审核发送
import XActivEmailToexamine  from "./Member/ActivEmailToexamine.vue"; //邮件审核
import XNinBlacklist  from "./Member/NinBlacklist.vue"; //黑名单管理
import XNinBlacklistIp  from "./Member/NinBlacklistIp.vue"; //封禁IP地址
import XWhitelist  from "./Member/Whitelist.vue"; //白名单管理
import XAdmin from "./Member/Admin.vue" //管理员---管理员列表
import XAdminJurisdiction from "./Member/AdminJurisdiction.vue" //管理员---角色权限
import XEchartsLine  from "./Section/EchartsLine.vue"; //Echarts


export default {
  name:"maina",
  components:{
      Xhead,
      XmainLeft,
      XServer,
      XServerAuditing,
      XformTable,
      XEchartsLine,
      XMemberList,
      XActivNotice,
      XActivEmail,
      XActivNoticeToexamine,
      XActivEmailToexamine,
      XNinBlacklist,
      XNinBlacklistIp,
      XWhitelist,
      XAdmin,
      XAdminJurisdiction
    },
  data () {
    return {
        RightActive:null,
        Listactive:null,
        screenHeight: document.documentElement.clientHeight - 90,//屏幕高度
        date: new Date(),
        getMonth:null ,//月份处理 
        getDay:null ,// 周期处理
        getSeconds:null,
        Navigation:null //导航处理
     
    }
  },
  mounted() {
      var _this = this;
      window.onresize = function(){ // 定义窗口大小变更通知事件
        _this.screenHeight = document.documentElement.clientHeight -90
      };
      this.timer = setInterval(function() {
                let MYtime = new Date();//修改数据date    
                let arr=["星期天","星期一","星期二","星期三","星期四","星期五","星期六"]
                _this.getMonth = (MYtime.getMonth() + 1 ) > 9 ? (MYtime.getMonth() + 1):'0' + ( MYtime.getMonth() + 1 )
                _this.getSeconds = MYtime.getSeconds()  > 9 ? MYtime.getSeconds() :'0' + MYtime.getSeconds()  
                _this.getDay =  arr[parseInt( MYtime.getDay() )] 
                _this.date  =  MYtime
      },1000); 
  },    
  created(){   
      var _this = this;
      let state =  this.$storage.getSession('state')
      if(state != '' && state != undefined && state != null){
          _this.RightActive =  state.RightActive
          _this.Listactive =  state.Listactive
      }  
      let MainJison = MainLeftjison 
      let km = MainLeftjison[_this.RightActive].name
      let kn = MainLeftjison[_this.RightActive].type[ _this.Listactive].name
      this.Navigation = ` ${km} => ${kn} `
     
  }, 
  methods: {
     animation(item,index){
        this.RightActive = item.RightActive
        this.Listactive = 0 
        this.$storage.setSession("state", {RightActive:this.RightActive,Listactive:this.Listactive}); 
        let km = MainLeftjison[this.RightActive].name
        let kn = MainLeftjison[this.RightActive].type[this.Listactive].name
        this.Navigation = ` ${km} => ${kn} `
    },
     listanim(Listactive,index){ 
       
        this.Listactive = Listactive;
        this.$storage.setSession("state", {RightActive:this.RightActive,Listactive:this.Listactive}); 
        let km = MainLeftjison[this.RightActive].name
        let kn = MainLeftjison[this.RightActive].type[this.Listactive].name
        this.Navigation = ` ${km} => ${kn} `
    }
  }
}
