import XTime from "@/components/Section/Time.vue";

import Card from "@/assets/json/card.json";
import Prop from "@/assets/json/Prop.json";
import { pushNotices , Users , postMails} from "@/api/api";
export default {
  name: "ActivNoticeDetails",
  components: { XTime },
  data() {
    return {
        TitleName: "邮件",
        name:"",
        userid:"",
        lastlogin:"",
        Tbody: ["玩家名称", "玩家ID", "玩家账号"],
        Tabelekey: false, //收件人选择open
        TableAcitve:false,//全选切换
        Tablelist: [], //玩家列表
        Severlist: [], //玩家选中
        TimeDaty: false, //时间组件
        Kbody: ["道具名称", "道具ID"],
        configkey:false, //道具选择open
        configAcitve:false,//道具全选 
        configureList:[], //道具列表
        configbox:[],
        carlId:null,
      screenHeight: document.documentElement.clientHeight - 250, //屏幕高度
      date: new Date(),
      getMonth: null, //月份处理
      getDay: null, // 周期处理
      getSeconds: null,
      size: 0,
      limit:999999999999,
      ticesTitle: "",
      ticesContent: ""
    };
  },
  created() {
    
    let ObjType = this.$route.query.Obj;
     this.User()
     this.configure()
  },
  mounted() {
    var _this = this;
    window.onresize = function() {
      // 定义窗口大小变更通知事件
      _this.screenHeight = document.documentElement.clientHeight - 250;
    };
    this.timer = setInterval(function() {
      let MYtime = new Date(); //修改数据date
      let arr = [
        "星期天",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六"
      ];
      _this.getMonth =
        MYtime.getMonth() + 1 > 9
          ? MYtime.getMonth() + 1
          : "0" + (MYtime.getMonth() + 1);
      _this.getSeconds =
        MYtime.getSeconds() > 9
          ? MYtime.getSeconds()
          : "0" + MYtime.getSeconds();
      _this.getDay = arr[parseInt(MYtime.getDay())];
      _this.date = MYtime;
    }, 1000);

  },
  methods: {
     async User(){
       let res = await Users({
            page:1,
            limit:this.limit,
            name:this.name,
            userid:this.userid,
       }).then(res=>{
           console.log(res.data,"@@@")
            this.Tablelist = []
            res.data.map(item=>{
                item.mous = false;
                item.tion = false;
                if(item.name!='' && item.name.userid !='' ){
                     this.Tablelist.push(item)
                }
            })
 
       })
     },
     ServeUser(){
       this.User()
     },
     serach(){
         let a = []
         let id = this.carlId 
         if(id == null || id == ''){
           this.configure()
           return
         }else{
           this.configureList.map(res=>{
              if(res.ID == id){
                 a.push(res)
              }
           })
         }
         this.configureList = a
     },
     configure(){
       let a = Card
       this.configureList = a.concat(Prop)
       this.configureList.map(item=>{
                item.mous = false;
                item.tion = false;
                item.size = 1
                
        })
     },
    ticTimes() {
      let reg = /^\d+\.?(\d{1,2})?$/;
      if (!reg.test(this.ticesTimes)) {
        this.ticesTimes = "";
        return;
      }
    },
   async  pushTicNotices() { //提交邮件审核 。。。。。
      if (this.ticesTitle == "") {
        alert("请输入公告标题");
        return;
      }
      let send_type   //类型
      let users = []  //选择玩家
      console.log(this.Severlist,"选择人员")
      if(this.Severlist =='' ){
         alert("请选择收件人")
         return
      }else{
           if(this.Severlist[0].name == "所有人"){
              send_type = 'all'
            }else{
              send_type = 'part'
              this.Severlist.map(item=>{
                    users.push(item.userid)
              })
            }
      }
     
     
      let accessories = []
      this.configbox.map(item=>{
           let a= {id:Number(item.ID),num:item.size}
           accessories.push(a)
       })
         if(this.ticesTitle.length > 8 ){
          alert("邮件标题最多为8个字符")
          return
        }
        if(this.ticesContent.length > 160 ){
          alert("邮件内容最多为160个字符")
          return
        }
       
       let res = await postMails({
            title:this.ticesTitle,
            content:this.ticesContent,
            accessories:accessories,
            send_type:send_type,
            users:users,
       }).then(res=>{
         
            alert("提交成功")
             this.$router.go(-1);
       }) 
    },
    
    
    //时间组件
    OpenTime() {
      this.TimeDaty = true;
    },
    cancel() {
      this.TimeDaty = false;
    },
    //添加收件人
    Noperation(){
       this.Tabelekey = false ;
       this.configkey = false
    },
    choiceTable() {
        this.configkey = false
        
     
       this.Tabelekey = !  this.Tabelekey ;
    },
    addConfig(){
      this.Tabelekey = false ;
      this.configkey = !  this.configkey 
    },
    choiceWhole(){ //选择所有人
       this.KeySwitch()
    },
    ToSwitch(item, index) {
      this.Severlist = [];
      this.Tablelist = [...this.Tablelist];
      this.Tablelist[index].tion = !this.Tablelist[index].tion;
      console.log(this.Tablelist[index].tion);
      this.Tablelist.map(time => {
        if (time.tion) {
          this.Severlist.push(time);
        }
      });
      console.log(this.Severlist.length);
    },
    KeySwitch() { //玩家全选 
      this.Severlist = [];
      this.Tablelist = [...this.Tablelist];
      this.TableAcitve = !this.TableAcitve;
      if (!this.TableAcitve) {
        this.Tablelist.map(time => {
          time.tion = false;
        });
        return;
      }else{ //全部选择
         let a = [{name:"所有人"}]
        this.Severlist = a
        this.Tablelist.map(time => {
            time.tion = true;
        });
      }
    },
    splice(index){
        if(this.configbox.length <= 1){
                     this.configbox = []
                     return
                }else{
                    this.configbox.splice(index,1)
                }
    },
    ToconfigSwitch(item,index){
           console.log(item)
           this.configureList = [...this.configureList];
            this.configbox = [...this.configbox];
           this.configureList[index].tion = !this.configureList[index].tion;
           if(item.tion){
               if( this.configbox == ''){
                     this.configbox.push(item)
                     return
               }
               let a 
                this.configbox.map(res=>{
                    if(res.ID != item.ID){
                        a = item
                    }
                })
                this.configbox.push(a)
                return
           }else{
                if(this.configbox.length <= 1){
                     this.configbox = []
                     return
                }else{
                    console.log(this.configbox);
                       console.log(item.ID,"当前ID")
                    for(let i = 0;i< this.configbox.length ;i++){
                        if(this.configbox[i].ID === item.ID){
                            console.log(this.configbox[i].ID,"匹配ID")
                           this.configbox.splice(i,1) 
                        }
                    }
                }
           }
    },
    configSwitch(){ //道具
        this.configbox = [];
        this.configureList = [...this.configureList];
        this.configAcitve = !this.configAcitve;
        if (!this.configAcitve) {
            this.configureList.map(time => {
              time.tion = false;
            });
            return;
        }else{ //全部选择
            
            this.configureList.map(time => {
                time.tion = true;
                
                this.configbox.push(time)
            });
        }
    },
    Email(index) {
     
    }
  }
};
