import Tablejison from "@/assets/json/Table.json"; 
import { getMails , getMailsID , Users } from "@/api/api";
import Card from "@/assets/json/card.json";
import Prop from "@/assets/json/Prop.json";

export default {
  name: "ActivEmail",
  components:{},
  data() {  
    return {
      TableAcitve: false,
      screenHeight:null,
      Tbody: ["操作人员","邮件标题", "邮件ID", "邮件内容","邮件状态", "发送时间", ],
      Table: [],
      Tablelist: [],
      page: 1, //当前页数
      pages: null, //总页数
      conter: 10, //每页条数
      Total: null,//总条数
      Selection:[] ,//选中
      EmailOperation:false,
      title:"",
      EmadilData:null,
      configbox:[],
      PropList:[],
      UserList:[],
      UserListBox:[],
      MailsId:null
    };
  },
  created() {
      let clientHeight = document.documentElement.clientHeight;
      this.conter = Math.floor((clientHeight - 450) / 50);
       if(this.conter < 5){
        this.conter = 5
      }
      this.screenHeight = clientHeight - 370
      this.configure()
      this.Mails()
      this.User()
  },
  mounted() {},
  methods: {
    async User(){
       let res = await Users({
            page:1,
            limit:999999999999,
       }).then(res=>{
          this.UserList = res.data
       
       })
     },
    configure(){
       let a = Card
       this.PropList = a.concat(Prop)
       this.PropList.map(item=>{
                item.mous = false;
                item.tion = false;
                item.size = 1 
        })
     
     },
     
     async Mails (){
         let res = await getMails({
              page:this.page,
              limit:this.conter,
              title:this.title,
            
         }).then(res=>{
           
               res.data.map(item=>{
               item.tion = false
               item.check_status_name = "未审核";
               if(item.check_status == 1){
                 item.check_status_name = "已审核";
               }
                if(item.send_status == 1){
                 item.check_status_name = "已发送";
               }
               this.Tablelist = res.data
               this.Total = res.meta.total;
               this.pages = res.meta.last_page  
            })
         })
     },
    TotalPaging() {
      this.Mails()
    },
    Prev(){ //上一页    
          console.log("上一页")
          this.page --
          this.TotalPaging();
    },
    next(){//下一页
          console.log("下一页")
          this.page ++ 
          this.TotalPaging();
    },
    AllSwitch(){//全选全不选
        this.Tablelist = [...this.Tablelist]
        this.TableAcitve =! this.TableAcitve
        this.Selection = []
        if(this.TableAcitve){
            this.Tablelist.map(item=>{
               item.tion = true 
            })
            for(var i = 0;i< this.TableAcitve.length;i++){
                this.TableAcitve[i].dex = i
                this.Selection.push(item)
            }
            return
        }else{
           this.Tablelist.map(item=>{
               item.tion = false   
            }) 
        }
    },
    ToSwitch(index){//单选
           this.Tablelist = [...this.Tablelist]
           this.Tablelist[index].tion =! this.Tablelist[index].tion
            this.Selection = [] 
           this.Tablelist.map(item=>{
                if(item.tion){
                  item.dex = index
                  this.Selection.push(item)
                }
           })
           console.log( this.Selection)
    },
     enter(index){//鼠标移入
       this.Tablelist = [...this.Tablelist]
       this.Tablelist[index].mous = true
    },
    leave(){//鼠标移除
        this.Tablelist = [...this.Tablelist]
        this.Tablelist.map(item=>{
          item.mous = false
        })
    },
    KeySwitch(){

    },
    AddActivNotice(index){
        let Obj={
            type:"Email",
            obj:{},  
        } 
        this.$router.push({ path: "/ActivEmailDetails",query:{Obj:Obj}}); 
    },
    EmailConter(item){
        this.MailsId = item.id
        this.MailsData()

  
        this.configbox = [...this.configbox]
         let a = this.PropList;
         if(item.accessories != null){
            a.map(key=>{
              item.accessories.map(res=>{
                if(key.ID == res.id){
                  res.name = key.name
                
                      this.configbox.push(res)
                  }
              })
           })
         }else{
              this.configbox = []
         }
        this.EmailOperation = true    
        this.EmadilData = item  
    },
    async MailsData(){
        let res = await getMailsID({
            ID:this.MailsId
        }).then(res=>{
     
           this.UserListBox = []
           this.UserList.map(item=>{
               res.data.users.map(key=>{
                 if(key == item.userid){
                  this.UserListBox.push(item)
                 }
               })
           })
        })
     },
    Noperation(){
      this.EmailOperation = false
    },
  }
};
