import Axios from 'axios'
import Cookies from 'js-cookie'

Axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8'
Axios.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded'

//const baseURL='http://giggsgame.com'
const TOKEN_KEY='token'
class httpRequest {
    constructor() {
        this.options = {
            method: '',
            url:'',
            baseURL: ''
        }
        // 存储请求队列
        this.queue = {}
    }
    // 销毁请求实例
    destory(url){
        delete this.queue[url]
        const queue=Object.keys(this.queue);
        return queue.length
    }
    //请求拦截
    interceptors(instance){
        //请求拦截器 url
        var data = JSON.parse(localStorage.getItem('token'));
        instance.interceptors.request.use(config=>{
            // 得到token后将token作为请求头返回给后台
            if(data){
                config.headers['Authorization']='Bearer '+ data //取出Cookie
            }
            return config
        },error=>{
            console.log(error,"@@@@@@@@@@@@@@@@@@")
            return Promise.reject(error)
        })
        // 响应拦截器
        instance.interceptors.response.use(res=>{
            let {data}=res
            //const is=this.destory(url)//请求完成后销毁实例
            if(res.status == 200 || res.status ==201 ||  res.status ==204){
                res.msg ="成功"  
            }else{
                localStorage.removeItem('token');
                window.location.href=window.location.pathname+'#/'//跳转到登录页
            }
            return data 
        },error=>{
             if(error.response.status == 500){
                alert("服务器错误")
            }else{
                localStorage.removeItem('token');
                window.location.href=window.location.pathname+'#/'//跳转到登录页        
            }
            return Promise.reject(error)
        })
    }
    //创建请求实例
    create(url){
        let conf={
            baseURL:url,
            timeout:8000,//超时时间
            ithCredentials:true//跨域请求的时候携带cookie
        }
        return Axios.create(conf)
    }
    //请求实例
    request(options){ 
        let instance=this.create(options.baseURL)//创建axios 传入baseURL参数
        this.interceptors(instance,options.url)//用创建好的axios去做拦截
        options=Object.assign({},options)//axios的参数
        return instance(options) //传入参数，返回axios实例      
    }
}
export default httpRequest