import axios from '@/libs/api.request'
const baseURL='http://game-admin.giggsgame.com/api'
//http://game-admin.giggsgame.com
//'http://game-admin.jigesi.test/api'

//用户登录
export const  Login=data=>{
    return axios.request({ 
        url:`/auth/login`,
        data, 
        baseURL,
        responseType:"json",
        method:'POST',
    })
};
//管理员列表
export const  adminIstrators =data=>{
    return axios.request({
        url:`/administrators`,
        params:data,
        baseURL,
        responseType:"json",
        method:'GET',
    })
};
//添加管理员
export const  postAdminIstrators =data=>{
    return axios.request({
        url:`/administrators`,
        params:data,
        baseURL,
        responseType:"json",
        method:'Post',
    })
};
//编辑管理员
export const  putAdminIstrators =data=>{
    return axios.request({
        url:`/administrators/${data.ID}`,
        params:data,
        baseURL,
        responseType:"json",
        method:'PUT',
    })
};
