

import XHeader from 'vux/src/components/x-header/index.vue'

import { Login } from "@/api/api"
export default {
name: 'Sign',
components: {
XHeader
},
data() {
return{
name:'admin',
Verificat:"",
}
},
mounted() {
document.querySelector('body').setAttribute('style',  "background-image: url(" + require("../assets/img/timg.jpg") + ")")
},
beforeDestroy() {
document.querySelector('body').removeAttribute('style')
},
created() {
},
methods: {
async logins(){
let res = await Login({
username:this.name,
password:this.Verificat,
}).then(res=>{
console.log(res)
if (res.access_token) {
localStorage.setItem('token',JSON.stringify(res.access_token));
this.$router.push({ path: "/Index" })
this.$storage.setSession("state", {RightActive:1,Listactive:0});
}
})
.catch(err=>{
if(err.response.status == 422 || err.response.status == 401){
Dialog.alert({message: '检查填写信息' })
}else{
Dialog.alert({message: '网络异常，请稍后再试' })
}
})
}
},
}

