import Xhead  from "@/components/Section/head.vue";
export default {
    name: "ServerDetails",
    components:{Xhead},
    data() {  
       return {
               screenHeight: document.documentElement.clientHeight -85 ,//屏幕高度
                date: new Date(),
                getMonth:null ,//月份处理 
                getDay:null ,// 周期处理
                getSeconds:null,
                size:0,
                
       }
    },
    mounted() {
      var _this = this;
      window.onresize = function(){ // 定义窗口大小变更通知事件
        _this.screenHeight = document.documentElement.clientHeight -85
      };
       this.timer = setInterval(function() {
                let MYtime = new Date();//修改数据date    
                let arr=["星期天","星期一","星期二","星期三","星期四","星期五","星期六"]
                _this.getMonth = (MYtime.getMonth() + 1 ) > 9 ? (MYtime.getMonth() + 1):'0' + ( MYtime.getMonth() + 1 )
                 _this.getSeconds = MYtime.getSeconds()  > 9 ? MYtime.getSeconds() :'0' + MYtime.getSeconds()  
                _this.getDay =  arr[parseInt( MYtime.getDay() )]
                
                _this.date = MYtime
      }, 1000);
       this.drawLine();           
    },
    methods: {
        close(){
             this.$router.go(-1);
        },
        drawLine() {
             let MYtime = new Date();//修改数据date   
             let getMonth = (MYtime.getMonth() + 1 ) > 9 ? (MYtime.getMonth() + 1):'' + ( MYtime.getMonth() + 1 )
            let _this = this
            let myChart = this.$echarts.init(document.getElementById("myChart"));
            myChart.setOption({
                   title: {
                        text: `用户概况 ：${getMonth}月`
                    },
                    legend: {
                        
                        data:['上线人数','注册人数','付费人数','活跃人数','活跃付费人数']
                    },
                    tooltip : {
                        trigger: 'axis',
                         formatter: "{a}:{c}人",
                        axisPointer: {
                            type: 'cross',
                            label: {
                                backgroundColor: '#6a7985'
                            }
                        }
                    },
                    xAxis: {
                        type: 'category',
                        data: [`${getMonth}月1日`, `${getMonth}月2日`,`${getMonth}月3日`,`${getMonth}月4日`,`${getMonth}月5日`,`${getMonth}月6日`,`${getMonth}月7日`,`${getMonth}月8日`,`${getMonth}月9日`,`${getMonth}月10日`,`${getMonth}月11日`,`${getMonth}月12日`,`${getMonth}月13日`]
                    },
                    yAxis: {
                        type: 'value',
                        axisLabel: {
                            formatter: '{value}人 '
                        }
                    },
                    series: [{
                        name:'上线人数',
                        data: [220, 832, 901, 334, 1290, 1330, 1320, 832, 901, 334, 1290, 1330, 1320],
                        type: 'line'
                    },{
                        name:'注册人数',
                        data: [320, 932, 901, 534, 1090, 1330, 1320, 932, 901, 534, 1090, 1330, 1320],
                        type: 'line'
                    },
                    {
                        name:'付费人数',
                        data: [420, 732, 901, 434, 90, 1330, 1320 ,732, 901, 434, 90, 1330, 1320],
                        type: 'line'
                    },{
                        name:'活跃人数',
                        data: [520, 532, 901, 734, 190, 1330, 1320,532, 901, 734, 190, 1330, 1320],
                        type: 'line'
                    },{
                        name:'活跃付费人数',
                        data: [820, 932, 901, 734, 1290, 1330, 1320, 932, 901, 734, 1290, 1330, 1320],
                        type: 'line'
                    }]
            })
        }
    }
}
