import EBasicLineChart from '../Echarts/BasicLineChart.vue'
import EStackedareachart from '../Echarts/Stackedareachart.vue'
import EBarTDelay from '../Echarts/BarTDelay.vue'
import EBarGradual from '../Echarts/BarGradual.vue'
import EBarStacking from '../Echarts/BarStacking.vue'
import EBarPromote from '../Echarts/Barpromote.vue'
import EBarMixZoom from '../Echarts/BarMixZoom.vue'
import EBarByRow from '../Echarts/BarByRow.vue'
import EPieWithLegend from '../Echarts/PieWithLegend.vue'
import EPieNestedPies from '../Echarts/PieNestedPies.vue'
import EScatterStream from '../Echarts/ScatterStream.vue'
export default {
     name: "XBasicLineChart",
        components: {EBasicLineChart,EStackedareachart,EBarTDelay,EBarGradual,EBarStacking,EBarPromote,EBarMixZoom,EBarByRow,
            EPieWithLegend,EPieNestedPies,EScatterStream
        },
        data() {
            return {
                screenHeight:null,
                Fide:true,
                Echartactive:'BasicLineChart',
               
                EchartsList:[
                    {name:"折线数据图",key:"BasicLineChart"},
                    {name:"堆叠区域图",key:"Stackedareachart"},
                    {name:"柱状图动画延迟",key:"BarTDelay"},
                    {name:"柱状图渐变，缩放",key:"BarGradual"},
                    {name:"堆叠柱状图",key:"BarStacking"},
                     {name:"增进柱状图",key:"Barpromote"},
                    {name:"多堆滚动柱状图",key:"BarMixZoom"},
                    {name:"多标展示柱状图",key:"BarByRow"},
                    {name:"饼状折散图",key:"PieWithLegend"},
                    {name:"嵌套环形图",key:"PieNestedPies"},
                    {name:"极点散点图",key:"ScatterStream"}
                     
                ] 
            }
        },
        created() {
            let clientHeight = document.documentElement.clientHeight;
            this.screenHeight = clientHeight -170
        },
        mounted(){
           
        },
        methods: {
           Echar(index,item){
               
              this.Echartactive = item.key
              this.Fide = true
           },
            colose(){
                this.Fide = false
            },
            
        }
}
