import Tablejison from "@/assets/json/Table.json"; 
import { Notices , putNotices }  from "@/api/api" ;
export default {
  name: "ActivNoticeToexamine",
  components:{},
  data() {  
    return {
      TableAcitve: false,
      screenHeight:null,
      EmailOperation:false,
      Tbody: ["操作人员","公告标题", "公告ID","公告内容", "发送时间", "审核"],
      Table: [],
      Tablelist: [],
      page: 1, //当前页数
      pages: null, //总页数
      conter: 10, //每页条数
      Total: null,//总条数
      Selection:[] ,//选中
      title:"",
      EmailOperationDation:null,
    };
  },
  created() {
      this.Table = Tablejison;
      let clientHeight = document.documentElement.clientHeight;
      this.conter = Math.floor((clientHeight - 450) / 50);
      if(this.conter < 5){
        this.conter = 5
      }
      this.screenHeight = clientHeight - 370
      this.TotalPaging();
  },
  mounted() {},
  methods: {
    TotalPaging() {
      this.ActNotices()
    },
      async ActNotices(){
          let res = Notices({
              page:this.page,
              limit:this.conter,
              title:this.title,
              check_status:0,
          }).then(res =>{
            res.data.map(item=>{
               item.tion = false
               item.check_status_name = "未审核";
               if(item.check_status == 1){
                 item.check_status_name = "已审核";
               }
                if(item.check_status == 2){
                 item.check_status_name = "已结束";
               }
            })
               this.Tablelist = res.data

                  this.Total = res.meta.total;
                  this.pages = res.meta.last_page  
             console.log(res)
          })
    },
    Prev(){ //上一页    
          console.log("上一页")
          this.page --
          this.TotalPaging();
    },
    next(){//下一页
          console.log("下一页")
          this.page ++ 
          this.TotalPaging();
    },
    AddActivNotice(){
       let Obj={
            type:"Notice",
            obj:{},  
        } 
        this.$router.push({ path: "/ActivNoticeDetails",query:{Obj:Obj}}); 
    },
       //审核弹出框
   async  Noperation(item){
      this.EmailOperation = false
      if(item != 1){
          let res = await putNotices({
              ID:item.id,
              check_status:3,
            })
            .then (res=>{
              
               this.ActNotices()
               this.EmailOperation = false
            })
      }
    },
   async  OKperation(item){
         let res = await putNotices({
              ID:item.id,
              check_status:1,
            })
            .then (res=>{
              console.log(res)
               this.ActNotices()
              alert("审核通过............")
               this.EmailOperation = false
            })
        
    },
    
    Toexamine(item){
      console.log(item,"循坏次数")
      this.EmailOperationDation = item
      this.EmailOperation = true
     
    }
  }
};
