import Tablejison from "@/assets/json/Table.json"; 
import { Notices , putNotices , DeleteNotices }  from "@/api/api" ; 
export default {
  name: "ActivNotice",
  components:{},
  data() {  
    return {
      TableAcitve: false,
      screenHeight:null,
      Tbody: ["操作人员","公告标题", "公告ID", "审核状态","公告内容", "发送时间","结束时间","操作"],
      Table: [],
      Tablelist: [],
      page: 1, //当前页数
      pages: null, //总页数
      conter: 10, //每页条数 
      Total: null,//总条数
      Selection:[] ,//选中
      EmailOperation:false,
      EmailOperationDetails:null,
      title:"",
      check_status:"",
      timestamp:null,
    };
  },
  created() {
      this.Table = Tablejison;
      let clientHeight = document.documentElement.clientHeight;
      this.conter = Math.floor((clientHeight - 450) / 70);
      if(this.conter < 5){
        this.conter = 5
      }
      this.screenHeight = clientHeight - 370
      this.mytime();
      this.ActNotices()
  },
  mounted() {},
  methods: {
     mytime(){//获取当前时间
          let MYtime = new Date();//修改数据date    
          let Year = MYtime.getFullYear()   //年
          let Month =  MYtime.getMonth() + 1  ;//月
              Month =Month > 9 ?  Month :'0' + Month 
          let Day =   MYtime.getDate(); //日
              Day =  Day  > 9 ?  Day :'0' +  Day ;

           let Time =   MYtime.getHours();//时
               Time = Time  > 9 ?  Time :'0' +  Time ;

          let Minute =  MYtime.getMinutes() ; //分钟
              Minute =Minute  > 9 ?  Minute :'0' +  Minute ;

          let times = `${Year}-${Month}-${Day} ${Time}:${Minute}:00 ` ;
          
          this.timestamp = new Date(times).getTime();
         console.log( this.timestamp)
          
     },
     TotalPaging() {
        this.ActNotices()
     },
     highQuery(){
       this.page = 1
        this.ActNotices()
     },
    async ActNotices(){
          let res = Notices({
              page:this.page,
              limit:this.conter,
              title:this.title,
              check_status:this.check_status
          }).then(res =>{
            res.data.map(item=>{

               item.tion = false
               item.check_status_name = "未审核";
              
                  ////////////////////////////

               if(item.check_status == 1){
                  let a =  new Date(item.end_at).getTime()
                  if(a > this.timestamp){
                        item.check_status_name = "已审核";
                    }else{
                      item.check_status = 2
                      item.check_status_name = "已结束";
                   }
                 
               }
                if(item.check_status == 2){
                 item.check_status_name = "已结束";
               }
                if(item.check_status == 3){
                 item.check_status_name = "审核未通过";
               }
            })
               this.Tablelist = res.data
               console.log(res.data)
                  this.Total = res.meta.total;
                  this.pages = res.meta.last_page  
            
          })
    },
    async outButton(item,index){ //修改公告
        if(item.check_status == 1){
            let res = await putNotices({
              ID:item.id,
              check_status:2
            })
            .then (res=>{
              this.Tablelist = [... this.Tablelist]
                this.Tablelist[index].check_status = 2
            })
        }
    },
    async cosonButton(item,inde){
            if(item.check_status !== 1 ){
                let res =await DeleteNotices({
                   ID:item.id
                }).then(res=>{
                    this.ActNotices()
                    alert("删除成功")                   
                }).catch(error =>{
                    alert("删除失败")
                })
            }
    },
    Prev(){ //上一页    
     
          this.page --
          this.ActNotices();
    },
    next(){//下一页
         
          this.page ++ 
          this.ActNotices();
    },
    AllSwitch(){//全选全不选
        this.Tablelist = [...this.Tablelist]
        this.TableAcitve =! this.TableAcitve
        this.Selection = []
        if(this.TableAcitve){
            this.Tablelist.map(item=>{
               item.tion = true 
            })
            for(var i = 0;i< this.TableAcitve.length;i++){
                this.TableAcitve[i].dex = i
                this.Selection.push(item)
            }
            return
        }else{
           this.Tablelist.map(item=>{
               item.tion = false   
            }) 
        }
    },
    ToSwitch(index){//单选
           this.Tablelist = [...this.Tablelist]
           this.Tablelist[index].tion =! this.Tablelist[index].tion
            this.Selection = [] 
           this.Tablelist.map(item=>{
                if(item.tion){
                  item.dex = index
                  this.Selection.push(item)
                }
           })
         
    },
    enter(index){//鼠标移入
       this.Tablelist = [...this.Tablelist]
       this.Tablelist[index].mous = true
    },
    leave(){//鼠标移除
        this.Tablelist = [...this.Tablelist]
        this.Tablelist.map(item=>{
          item.mous = false
        })
    },
    AddActivNotice(){
       let Obj={
            type:"Notice",
            obj:{},  
        } 
        this.$router.push({ path: "/ActivNoticeDetails",query:{Obj:Obj}}); 
    },
    KeySwitch(){
             
    },
    Noperation(){
      this.EmailOperation = false
    },
    NotConten(item){
       console.log(item)
       this.EmailOperation = true
       this.EmailOperationDetails = item
    }
  }
};
