export default {
     name: "BasicLineChart",
        components: {},
        data() {
            return {}
        },
        created() { 
        },
        mounted(){
            this.drawLine();
        },
        methods: {
          
            drawLine(){
               
                let myChart = this.$echarts.init(document.getElementById('myChart'))
                // 绘制图表
                myChart.setOption({
                    xAxis: {
                        type: 'category',
                        data: ['星期一', '星期二', '星期三', '星期四', '星期五', '星期六', '星期天']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    olor: 'blue',
                    series: [{
                        data: [820, 932, 901, 934, 1290, 1330, 1320],
                        type: 'line'
                    }]
                });
          
                let myChart1 = this.$echarts.init(document.getElementById('myChart1'))
                 myChart1.setOption({
                   xAxis: {
                        type: 'category',
                        boundaryGap: false, //XXXXXXXXXX
                        data:  ['星期一', '星期二', '星期三', '星期四', '星期五', '星期六', '星期天']
                    },
                    yAxis: {
                        type: 'value'
                    },
                     color: '#33333D',
                    series: [{
                        data: [820, 932, 901, 934, 1290, 1330, 1320],
                        type: 'line',
                        areaStyle: {}
                    }]
                });

                 let myChart2 = this.$echarts.init(document.getElementById('myChart2'))
                  var app = {};
                    var option = null;
                    var base = +new Date(1968, 9, 3);
                    var oneDay = 24 * 3600 * 1000;
                    var date = [];

                    var data = [Math.random() * 300];

                    for (var i = 1; i < 20000; i++) {
                        var now = new Date(base += oneDay);
                        date.push([now.getFullYear(), now.getMonth() + 1, now.getDate()].join('/'));
                        data.push(Math.round((Math.random() - 0.5) * 20 + data[i - 1]));
                    }
                 myChart2.setOption({
                      tooltip: {
                        trigger: 'axis',
                        position: function (pt) {
                            return [pt[0], '10%'];
                        }
                    },
                    title: {
                        left: 'center',
                        text: '大数据量面积图',
                    },
                    toolbox: {
                        feature: {
                            dataZoom: {
                                yAxisIndex: 'none'
                            },
                            restore: {},
                            saveAsImage: {}
                        }
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: date
                    },
                    yAxis: {
                        type: 'value',
                        boundaryGap: [0, '100%']
                    },
                    dataZoom: [{
                        type: 'inside',
                        start: 0,
                        end: 10
                    }, {
                        start: 0,
                        end: 10,
                        handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
                        handleSize: '80%',
                        handleStyle: {
                            color: '#fff',
                            shadowBlur: 3,
                            shadowColor: 'rgba(0, 0, 0, 0.6)',
                            shadowOffsetX: 2,
                            shadowOffsetY: 2
                        }
                    }],
                    series: [
                        {
                            name:'模拟数据',
                            type:'line',
                            smooth:true,
                            symbol: 'none',
                            sampling: 'average',
                            itemStyle: {
                                color: 'rgb(0,126,125)'
                            },
                            areaStyle: {
                                color: this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                    offset: 0,
                                    color: 'rgb(0,126,125)'
                                }, {
                                    offset: 1,
                                    color: 'rgb(0,126,125)'
                                }])
                            },
                            data: data
                        }
                    ]
                 })

                 let myChart3 = this.$echarts.init(document.getElementById('myChart3'))
                 var data = [];
                for (var i = 0; i <= 360; i++) {
                    var t = i / 180 * Math.PI;
                    var r = Math.sin(2 * t) * Math.cos(2 * t);
                    data.push([r, i]);
                }
                  myChart3.setOption({
                         title: {
                            text: '极坐标双数值轴'
                        },
                        legend: {
                            data: ['line']
                        },
                        polar: {
                            center: ['50%', '54%']
                        },
                        tooltip: {
                            trigger: 'axis',
                            axisPointer: {
                                type: 'cross'
                            }
                        },
                        angleAxis: {
                            type: 'value',
                            startAngle: 0
                        },
                        radiusAxis: {
                            min: 0
                        },
                        series: [{
                            coordinateSystem: 'polar',
                            name: 'line',
                            type: 'line',
                            showSymbol: false,
                            data: data
                        }],
                        animationDuration: 2000
                  })
               
                
            }
        }
}
