import XTime from "@/components/Section/Time.vue";
import Tablejison from "@/assets/json/Table.json";
import { pushNotices } from "@/api/api";
export default {
  name: "ActivNoticeDetails",
  components: { XTime },
  data() {
    return {
      Tabelekey: false,
      TimeDaty: false,
      TableAcitve: false,
      objActive: true,
      TitleName: "公告",
      screenHeight: document.documentElement.clientHeight - 250, //屏幕高度
      date: new Date(),
      getMonth: null, //月份处理
      getDay: null, // 周期处理
      getSeconds: null,
      size: 0,
      Tbody: ["服务器编号", "服务器名称", "服务器状态"],
      Tablelist: [],
      TableAcitve: false,
      Severlist: [],
      ticesTitle: "",
      ticesSpace: "30", //循环间隔时间
      ticesTimes: "3",  //循序播放次数
      
      UserState:1,
      level:"",
      ticesContent: ""
    };
  },
  created() {
    Tablejison.map(item => {
      item.mous = false;
      item.tion = false;
    });
    this.Tablelist = Tablejison;

    let ObjType = this.$route.query.Obj;
    if (ObjType.type == "Email") {
      (this.objActive = false), (this.TitleName = "邮件");
    }
  },
  mounted() {
    var _this = this;
    window.onresize = function() {
      // 定义窗口大小变更通知事件
      _this.screenHeight = document.documentElement.clientHeight - 250;
    };
    this.timer = setInterval(function() {
      let MYtime = new Date(); //修改数据date
      let arr = [
        "星期天",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六"
      ];
      _this.getMonth =
        MYtime.getMonth() + 1 > 9
          ? MYtime.getMonth() + 1
          : "0" + (MYtime.getMonth() + 1);
      _this.getSeconds =
        MYtime.getSeconds() > 9
          ? MYtime.getSeconds()
          : "0" + MYtime.getSeconds();
      _this.getDay = arr[parseInt(MYtime.getDay())];
      _this.date = MYtime;
    }, 1000);
  },
  methods: {
    Sure(item){
       this.level = item
       this.TimeDaty = false
    },
    ticTimes() {
      let reg = /^\d+\.?(\d{1,2})?$/;
      if (!reg.test(this.ticesTimes)) {
        this.ticesTimes = "";
        return;
      }
    },
    async pushTicNotices() {
      if (this.ticesTitle == "") {
        alert("请输入公告标题");
        return;
      }
      if (this.ticesContent == "") {
        alert("请输入公告内容");
        return;
      }
      if (this.ticesSpace == "") {
        this.ticesSpace == 30;
      }
      if (this.ticesTimes == "") {
        this.ticesTimes == 3;
      }

      
      if(this.level == ""){
        this.level ='2020-04-28 11:59:37'
      }
      let res = await pushNotices({
        title: this.ticesTitle,
        content: this.ticesContent,
        play_space: this.ticesSpace,
         play_times: this.ticesTimes,
        end_at:this.level,
        level:this.UserState,
       
      }).then(res => {
        console.log(res);
        alert("提交成功");
        this.$router.go(-1);
      });
    },
    enter(index) {
      //鼠标移入
      this.Tablelist = [...this.Tablelist];
      this.Tablelist[index].mous = true;
    },
    leave() {
      //鼠标移除
      this.Tablelist = [...this.Tablelist];
      this.Tablelist.map(item => {
        item.mous = false;
      });
    },
    //时间组件
    OpenTime() {
      this.TimeDaty = true;
    },
    cancel() {
      this.TimeDaty = false;
    },
    //表单Tabelekey
    OpenTable() {
      this.Tabelekey = true;
    },
    ToSwitch(item, index) {
      this.Severlist = [];
      this.Tablelist = [...this.Tablelist];
      this.Tablelist[index].tion = !this.Tablelist[index].tion;
      console.log(this.Tablelist[index].tion);
      this.Tablelist.map(time => {
        if (time.tion) {
          this.Severlist.push(time);
        }
      });
      console.log(this.Severlist.length);
    },
    KeySwitch() {
      this.Severlist = [];
      this.Tablelist = [...this.Tablelist];
      this.TableAcitve = !this.TableAcitve;
      if (!this.TableAcitve) {
        this.Tablelist.map(time => {
          time.tion = false;
        });
        return;
      }
      this.Tablelist.map(time => {
        time.tion = true;
        if (time.tion) {
          this.Severlist.push(time);
        }
      });
    },
    Email(index) {
      if (index == 0) {
        (this.objActive = true), (this.TitleName = "公告");
      } else {
        (this.objActive = false), (this.TitleName = "邮件");
      }
    }
  }
};
